<template>
    <div class="konten">
        <div class="d-flex align-items-center fixed-top border-bottom shadow-sm py-2 px-2 bg-white justify-content-between container-header">
            <span @click="$router.back()" class="fas fa-arrow-left text-black-50 pl-2"></span>
            <form @submit.prevent="setSearch()">
				<div class="input-group mx-2">
					<input type="search" @change="search = $event.target.value" :value="$route.query.q ? $route.query.q : ''" class="form-control border-warning" placeholder="Coba cari nasi goreng">
					<div class="input-group-append">
						<button type="submit" class="btn btn-warning text-white">Cari</button>
					</div>
				</div>
			</form>
			<ul class="nav text-nowrap" v-if="$route.name!='login' && $route.name!='register' && $route.name!='forgot' && $route.name!='cart'">
                <li class="nav-item">
                    <a v-if="$store.state.cart.cart_product==null&&$store.state.login.ifLogin" href="#" class="nav-link">
                        <i class="fa fa-spinner fa-spin"></i>
                    </a>
                    <router-link v-else class="nav-link" :to="{name:'cart',query:{from:$route.name}}">
                        <i class="fas fa-shopping-cart"></i>
                        <span class="badge count_cart" >{{$store.getters.cart_product_length}}</span>
                    </router-link>
                </li>
            </ul>
            <ul v-if="$route.name=='login' || $route.name=='register' || $route.name=='forgot' || $route.name=='cart'"></ul>
        </div>
		<div v-if="product==null&&busy" class="row mt-3 pt-5">
			<img :src="$store.state.url_images+'loading.svg'" class="img-fluid d-flex mx-auto" style="height: 100px">
		</div>
		<div class="row pt-5 mt-2" v-if="product!=null">
			<div class="col" v-if="$store.getters.product_search_length==0">
				<div class="d-flex align-items-center py-5 mt-5">
					<div class="col-4">
						<img src="@/assets/noSearch.svg" alt="Pencarian tukarjual.com" class="img-fluid">
					</div>
					<div class="col-8">
						<h5>Wah, coba kata kunci lain</h5>
						<small>Yuk, cari barang kebutuhanmu yang lain</small>
					</div>
				</div>
				<!-- <input type="button" value="Mulai Belanja" class="btn btn-info btn-block tombol"> -->
			</div>
			<template v-else>
				<div class="col-4 p-1" v-for="(data, key) in product" :key="'product'+key">
					<router-link :to="{name:'Product',params:{kategori:data.kategori, kategori_id:data.id_kategory_barang, product_id:data.id_barang,product:data.seo_nama_barang}}">
						<div class="card product">
							<img v-if="data.depan!=null&&data.depan!=''" class="lazy img-fluid card-img-top" :src="$store.state.url_images+'loading_post_produk.svg'" :data-src="$store.state.url_product+data.depan" :alt="data.nama_barang">
							<img v-else class="img-fluid card-img-top" :src="$store.state.url_images+'default_image.jpeg'" :alt="data.nama_barang">
							<div class="card-body">
								<p class="product-name small">{{data.nama_barang}}</p>
								<strong class="small font-weight-bold text-success">
									<template>
										Rp{{toCurrency(data.harga_barang)}}
									</template>
								</strong>
								<br>
								<router-link :to="{name:'Store',params:{store:data.penjual_model.seo_nama_toko}}" class="small text-muted sell-name">{{data.penjual_model.nama_toko}}</router-link>
							</div>
						</div>
					</router-link>
				</div>
			</template>
		</div>
    </div>
</template>
<script>
export default {
	data(){
        return {
			busy:false,
			search:this.$route.query.q ? this.$route.query.q : '',
			limit:false
        }
	},
	computed:{
		query(){
			if(this.$route.query.q!=undefined){
				return this.$route.query.q;
			}else{
				return '';
			}
		},
		product(){
			return this.$store.state.search.product
		}
	},
    async created(){
		this.$store.dispatch('reset_product_search_to_null')
		if(this.query!=''){
			if(this.product==null){
				await this.data_product(this.query);
			}
		}
		window.addEventListener('scroll', this.loadMore)
	},
	destroyed () {
		window.removeEventListener('scroll',this.loadMore)
	},
    async updated(){
        await this.lazyLoad()
    },
    methods:{
		loadMore(){
			if(this.product!=null&&!this.busy){
					
				var d = document.documentElement;
				var offset = d.scrollTop + window.innerHeight;
				var height = d.offsetHeight;
			
				if (offset >= height-50) {
					if(!this.limit){
						this.data_product(this.query);
					}
				}
			}
		},
        lazyLoad() {
			var lazyloadImages;

			if ("IntersectionObserver" in window) {
				lazyloadImages = document.querySelectorAll(".lazy");
				var imageObserver = new IntersectionObserver(function (entries) {
					entries.forEach(function (entry) {
						if (entry.isIntersecting) {
							var image = entry.target;
							image.src = image.dataset.src;
							image.classList.remove("lazy");
							imageObserver.unobserve(image);
						}
					});
				});

				lazyloadImages.forEach(function (image) {
					imageObserver.observe(image);
				});
            }
		},
        toCurrency(angka) {
			angka += '';
			// angka = angka.replace(".",",");
			var x = angka.split('.');
			var x1 = x[0];
			var x2 = x.length > 1 ? '.' + x[1] : '';
			var rgx = /(\d+)(\d{3})/;
			while (rgx.test(x1)) {
				x1 = x1.replace(rgx, '$1' + '.' + '$2');
			}
			return x1 + x2;
		},
        data_product(query){
			this.busy = true;
			var url = "";
			var headers = {};
            if(this.$store.state.login.ifLogin){
				url = this.$store.state.url_api + "product/search?query="+query+"&cur="+this.$store.getters.product_search_length;
				headers = {
					"Authorization": "Bearer " + localStorage.token,
				};
					
            }else{
                url = this.$store.state.url_api + "product/search?query="+query+"&cur="+this.$store.getters.product_search_length;
				headers = {
					"Content-Type":"application/json" ,
				};
			}	
			this.axios.get(url,{
				headers: headers,
			}).then(response => {
				if(response.data.length<1){
					this.limit = true;
				}
				response.data.sort(function(a,b){
					return b.analytics_barang_model.cta-a.analytics_barang_model.cta || b.id_barang-a.id_barang;
				})
				if(this.$store.state.search.product==null){
					this.$store.dispatch('reset_product_search');
				}
				this.$store.dispatch('save_product_search', response.data);
				this.busy = false;
			})
		},
		setSearch(){
			if(this.search.trim() != this.query.trim()){
				this.limit = false;
				this.$store.dispatch('reset_product_search_to_null');
				this.data_product(this.search);
				this.$router.replace({
					name:'search',
					query:{
						q:this.search
					}
				})
			}
			
		}
	}
}
</script>